.inputContainer {
  margin-bottom: var(--input-container-margin-bottom);
}

.input {
  border: var(--input-border);
  border-radius: var(--input-border-radius);
  font-size: var(--input-font-size);
  height: var(--input-height);
  margin: var(--input-margin);
  padding: var(--input-padding);
  width: var(--input-desktop-width);
}
.input:focus-visible {
  border: var(--input-border-focus);
}
.input:hover {
  border: var(--input-border-hover);
}
.input:disabled {
  background-color: var(--utility-neutral-05);
}
.input:disabled:hover {
  border: var(--input-border);
}
@media (max-width: 500px) {
  .input {
    width: 100%;
  }
}

.input[aria-invalid=true] {
  border-color: var(--input-invalid-border-color);
  border-left-width: var(--input-invalid-border-left-width);
}

.inputContainer,
.inputInnerContainer,
.input {
  width: var(--input-desktop-width);
}
@media (max-width: 500px) {
  .inputContainer,
  .inputInnerContainer,
  .input {
    width: 100%;
  }
}

.inputContainer {
  margin-bottom: var(--input-container-margin-bottom);
}
.inputContainer .inputInnerContainer {
  margin: var(--input-margin);
  position: relative;
}
.inputContainer .inputInnerContainer .inputAdornment {
  align-items: center;
  display: flex;
  font-size: var(--input-font-size);
  height: 100%;
  margin: 0 var(--spacing-s);
  position: absolute;
}
.inputContainer .inputInnerContainer .input {
  padding-left: var(--spacing-l);
}